import React from 'react'
import { push } from 'connected-react-router'
import { bindActionCreators } from 'redux'
import { connect } from 'react-redux'
import {
  increment,
  incrementAsync,
  decrement,
  decrementAsync
} from '../../modules/counter'

import {
  getNoteAsync
} from '../../modules/notes'

const Home = props => (
  <div>
    <h1></h1>
	
<form action="" method="get">
  <input name="s" placeholder=" ..." type="search" />
  <button type="submit"></button>
</form>
	
	
    <p>Count: {props.count}</p>
	
	<p>Note: {JSON.stringify(props.note)} <button onClick={props.getNoteAsync} disabled={props.isNote}>getNoteAsync</button></p>

    <p>
      <button onClick={props.increment}>Increment</button>
      <button onClick={props.incrementAsync} disabled={props.isIncrementing}>
        Increment Async
      </button>
    </p>

    <p>
      <button onClick={props.decrement}>Decrement</button>
      <button onClick={props.decrementAsync} disabled={props.isDecrementing}>
        Decrement Async
      </button>
    </p>

    <p>
      <button onClick={() => props.changePage()}>
        Go to about page via redux
      </button>
    </p>
  </div>
)

const mapStateToProps = ({ counter, notes  }) => ({
  count: counter.count,
  isIncrementing: counter.isIncrementing,
  isDecrementing: counter.isDecrementing,
  
  notes: notes.notes,
  note: notes.note,
  isNotes: notes.isNotes,
  isNote: notes.isNote
})

const mapDispatchToProps = dispatch =>
  bindActionCreators(
    {
      increment,
      incrementAsync,
      decrement,
      decrementAsync,
      changePage: () => push('/about-us'),
	  
      getNoteAsync
    },
    dispatch
  )

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Home)



